 {\n      content: '\\00a0';\n    }\n\n    th,\n    td {\n      margin: 0;\n      vertical-align: top;\n    }\n\n    th { text-align: left; }\n    thead {\n      display: block;\n      float: left;\n\n      tr {\n        display: block;\n        padding: 0 10px 0 0;\n\n        th::before {\n          content: \"\\00a0\";\n        }\n      }\n    }\n    tbody {\n      display: block;\n      width: auto;\n      position: relative;\n      overflow-x: auto;\n      white-space: nowrap;\n\n      tr {\n        display: inline-block;\n        vertical-align: top;\n      }\n    }\n    th {\n      display: block;\n      text-align: right;\n    }\n    td {\n      display: block;\n      min-height: 1.25em;\n      text-align: left;\n    }\n    tr { padding: 0 10px; }\n\n    /* sort out borders */\n    thead {\n      border: 0;\n      border-right: 1px solid $table-border-color;\n    }\n\n    &.bordered {\n      th { border-bottom: 0; border-left: 0; }\n      td { border-left: 0; border-right: 0; border-bottom: 0; }\n      tr { border: 0; }\n      tbody tr { border-right: 1px solid $table-border-color; }\n    }\n\n  }\n\n}\n\n\n// Collections\n.collection {\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  border: 1px solid $collection-border-color;\n  border-radius: 2px;\n  overflow: hidden;\n  position: relative;\n\n  .collection-item {\n    background-color: $collection-bg-color;\n    line-height: $collection-line-height;\n    padding: 10px 20px;\n    margin: 0;\n    border-bottom: 1px solid $collection-border-color;\n\n    // Avatar Collection\n    &.avatar {\n      min-height: 84px;\n      padding-left: 72px;\n      position: relative;\n\n      .circle {\n        position: absolute;\n        width: 42px;\n        height: 42px;\n        overflow: hidden;\n        left: 15px;\n        display: inline-block;\n        vertical-align: middle;\n      }\n      i.circle {\n        font-size: 18px;\n        line-height: 42px;\n        color: #fff;\n        background-color: #999;\n        text-align: center;\n      }\n\n\n      .title {\n        font-size: 16px;\n      }\n\n      p {\n        margin: 0;\n      }\n\n      .secondary-content {\n        position: absolute;\n        top: 16px;\n        right: 16px;\n      }\n\n    }\n\n\n    &:last-child {\n      border-bottom: none;\n    }\n\n    &.active {\n      background-color: $collection-active-bg-color;\n      color: $collection-active-color;\n\n      .secondary-content {\n        color: #fff;\n      }\n    }\n  }\n  a.collection-item{\n    display: block;\n    transition: .25s;\n    color: $collection-link-color;\n    &:not(.active) {\n      &:hover {\n        background-color: $collection-hover-bg-color;\n      }\n    }\n  }\n\n  &.with-header {\n    .collection-header {\n      background-color: $collection-bg-color;\n      border-bottom: 1px solid $collection-border-color;\n      padding: 10px 20px;\n    }\n    .collection-item {\n      padding-left: 30px;\n    }\n    .collection-item.avatar {\n      padding-left: 72px;\n    }\n  }\n\n}\n// Made less specific to allow easier overriding\n.secondary-content {\n  float: right;\n  color: $secondary-color;\n}\n.collapsible .collection {\n  margin: 0;\n  border: none;\n}\n\n\n\n// Responsive Videos\n.video-container {\n    position: relative;\n    padding-bottom: 56.25%;\n    height: 0;\n    overflow: hidden;\n\n    iframe, object, embed {\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n    }\n}\n\n// Progress Bar\n.progress {\n    position: relative;\n    height: 4px;\n    display: block;\n    width: 100%;\n    background-color: lighten($progress-bar-color, 40%);\n    border-radius: 2px;\n    margin: $element-top-margin 0 $element-bottom-margin 0;\n    overflow: hidden;\n  .determinate {\n    position: absolute;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    background-color: $progress-bar-color;\n    transition: width .3s linear;\n  }\n  .indeterminate {\n    background-color: $progress-bar-color;\n    &:before {\n      content: '';\n      position: absolute;\n      background-color: inherit;\n      top: 0;\n      left:0;\n      bottom: 0;\n      will-change: left, right;\n      // Custom bezier\n      animation: indeterminate 2.1s cubic-bezier(0.650, 0.815, 0.735, 0.395) infinite;\n\n    }\n    &:after {\n      content: '';\n      position: absolute;\n      background-color: inherit;\n      top: 0;\n      left:0;\n      bottom: 0;\n      will-change: left, right;\n      // Custom bezier\n      animation: indeterminate-short 2.1s cubic-bezier(0.165, 0.840, 0.440, 1.000) infinite;\n      animation-delay: 1.15s;\n    }\n  }\n}\n@keyframes indeterminate {\n    0% {\n      left: -35%;\n      right:100%;\n    }\n    60% {\n      left: 100%;\n      right: -90%;\n    }\n    100% {\n      left: 100%;\n      right: -90%;\n    }\n}\n\n@keyframes indeterminate-short {\n    0% {\n      left: -200%;\n      right: 100%;\n    }\n    60% {\n      left: 107%;\n      right: -8%;\n    }\n    100% {\n      left: 107%;\n      right: -8%;\n    }\n}\n\n\n/*******************\n  Utility Classes\n*******************/\n\n.hide {\n  display: none !important;\n}\n\n// Text Align\n.left-align {\n  text-align: left;\n}\n.right-align {\n  text-align: right\n}\n.center, .center-align {\n  text-align: center;\n}\n\n.left {\n  float: left !important;\n}\n.right {\n  float: right !important;\n}\n\n// No Text Select\n.no-select {\n  -webkit-touch-callout: none;\n  -webkit-user-select: none;\n  -khtml-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n\n.circle {\n  border-radius: 50%;\n}\n\n.center-block {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.truncate {\n  display: block;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.no-padding {\n  padding: 0 !important;\n}\n","// ==========================================================================\n// Materialize variables\n// ==========================================================================\n//\n// Table of Contents:\n//\n//  1. Colors\n//  2. Badges\n//  3. Buttons\n//  4. Cards\n//  5. Collapsible\n//  6. Chips\n//  7. Date Picker\n//  8. Dropdown\n//  10. Forms\n//  11. Global\n//  12. Grid\n//  13. Navigation Bar\n//  14. Side Navigation\n//  15. Photo Slider\n//  16. Spinners | Loaders\n//  17. Tabs\n//  18. Tables\n//  19. Toasts\n//  20. Typography\n//  21. Footer\n//  22. Flow Text\n//  23. Collections\n//  24. Progress Bar\n\n\n\n// 1. Colors\n// ==========================================================================\n\n$primary-color: color(\"materialize-red\", \"lighten-2\") !default;\n$primary-color-light: lighten($primary-color, 15%) !default;\n$primary-color-dark: darken($primary-color, 15%) !default;\n\n$secondary-color: color(\"teal\", \"lighten-1\") !default;\n$success-color: color(\"green\", \"base\") !default;\n$error-color: color(\"red\", \"base\") !default;\n$link-color: color(\"light-blue\", \"darken-1\") !default;\n\n\n// 2. Badges\n// ==========================================================================\n\n$badge-bg-color: $secondary-color !default;\n$badge-height: 22px !default;\n\n\n// 3. Buttons\n// ==========================================================================\n\n// Shared styles\n$button-border: none !default;\n$button-background-focus: lighten($secondary-color, 4%) !default;\n$button-font-size: 1rem !default;\n$button-icon-font-size: 1.3rem !default;\n$button-height: 36px !default;\n$button-padding: 0 2rem !default;\n$button-radius: 2px !default;\n\n// Disabled styles\n$button-disabled-background: #DFDFDF !default;\n$button-disabled-color: #9F9F9F !default;\n\n// Raised buttons\n$button-raised-background: $secondary-color !default;\n$button-raised-background-hover: lighten($button-raised-background, 5%) !default;\n$button-raised-color: #fff !default;\n\n// Large buttons\n$button-large-icon-font-size: 1.6rem !default;\n$button-large-height: $button-height * 1.5 !default;\n\n// Flat buttons\n$button-flat-color: #343434 !default;\n$button-flat-disabled-color: lighten(#999, 10%) !default;\n\n// Floating buttons\n$button-floating-background: $secondary-color !default;\n$button-floating-background-hover: $button-floating-background !default;\n$button-floating-color: #fff !default;\n$button-floating-size: 40px !default;\n$button-floating-large-size: 56px !default;\n$button-floating-radius: 50% !default;\n\n\n// 4. Cards\n// ==========================================================================\n\n$card-padding: 24px !default;\n$card-bg-color: #fff !default;\n$card-link-color: color(\"orange\", \"accent-2\") !default;\n$card-link-color-light: lighten($card-link-color, 20%) !default;\n\n\n// 5. Collapsible\n// ==========================================================================\n\n$collapsible-height: 3rem !default;\n$collapsible-line-height: $collapsible-height !default;\n$collapsible-header-color: #fff !default;\n$collapsible-border-color: #ddd !default;\n\n\n// 6. Chips\n// ==========================================================================\n\n$chip-bg-color: #e4e4e4 !default;\n$chip-border-color: #9e9e9e !default;\n$chip-selected-color: #26a69a !default;\n$chip-margin: 5px !default;\n\n\n// 7. Date Picker\n// ==========================================================================\n\n$datepicker-weekday-bg: darken($secondary-color, 7%) !default;\n$datepicker-date-bg: $secondary-color !default;\n$datepicker-year: rgba(255, 255, 255, .4) !default;\n$datepicker-focus: rgba(0,0,0, .05) !default;\n$datepicker-selected: $secondary-color !default;\n$datepicker-selected-outfocus: desaturate(lighten($secondary-color, 35%), 15%) !default;\n\n\n// 8. Dropdown\n// ==========================================================================\n\n$dropdown-bg-color: #fff !default;\n$dropdown-hover-bg-color: #eee !default;\n$dropdown-color: $secondary-color !default;\n$dropdown-item-height: 50px !default;\n\n\n// 9. Fonts\n// ==========================================================================\n\n$roboto-font-path: \"../fonts/roboto/\" !default;\n\n\n// 10. Forms\n// ==========================================================================\n\n// Text Inputs + Textarea\n$input-height: 3rem !default;\n$input-border-color: color(\"grey\", \"base\") !default;\n$input-border: 1px solid $input-border-color !default;\n$input-background: #fff !default;\n$input-error-color: $error-color !default;\n$input-success-color: $success-color !default;\n$input-focus-color: $secondary-color !default;\n$input-font-size: 1rem !default;\n$input-margin-bottom: 20px;\n$input-margin: 0 0 $input-margin-bottom 0 !default;\n$input-padding: 0 !default;\n$input-transition: all .3s !default;\n$label-font-size: .8rem !default;\n$input-disabled-color: rgba(0,0,0, .26) !default;\n$input-disabled-solid-color: #BDBDBD !default;\n$input-disabled-border: 1px dotted $input-disabled-color !default;\n$input-invalid-border: 1px solid $input-error-color !default;\n$placeholder-text-color: lighten($input-border-color, 20%) !default;\n\n// Radio Buttons\n$radio-fill-color: $secondary-color !default;\n$radio-empty-color: #5a5a5a !default;\n$radio-border: 2px solid $radio-fill-color !default;\n\n// Range\n$range-height: 14px !default;\n$range-width: 14px !default;\n$track-height: 3px !default;\n\n// Select\n$select-border: 1px solid #f2f2f2 !default;\n$select-background: rgba(255, 255, 255, 0.90) !default;\n$select-focus: 1px solid lighten($secondary-color, 47%) !default;\n$select-padding: 5px !default;\n$select-radius: 2px !default;\n$select-disabled-color: rgba(0,0,0,.3) !default;\n\n// Switches\n$switch-bg-color: $secondary-color !default;\n$switch-checked-lever-bg: desaturate(lighten($secondary-color, 25%), 25%) !default;\n$switch-unchecked-bg: #F1F1F1 !default;\n$switch-unchecked-lever-bg: #818181 !default;\n$switch-radius: 15px !default;\n\n\n// 11. Global\n// ==========================================================================\n\n// Media Query Ranges\n$small-screen-up: 601px !default;\n$medium-screen-up: 993px !default;\n$large-screen-up: 1201px !default;\n$small-screen: 600px !default;\n$medium-screen: 992px !default;\n$large-screen: 1200px !default;\n\n$medium-and-up: \"only screen and (min-width : #{$small-screen-up})\" !default;\n$large-and-up: \"only screen and (min-width : #{$medium-screen-up})\" !default;\n$extra-large-and-up: \"only screen and (min-width : #{$large-screen-up})\" !default;\n$small-and-down: \"only screen and (max-width : #{$small-screen})\" !default;\n$medium-and-down: \"only screen and (max-width : #{$medium-screen})\" !default;\n$medium-only: \"only screen and (min-width : #{$small-screen-up}) and (max-width : #{$medium-screen})\" !default;\n\n\n// 12. Grid\n// ==========================================================================\n\n$num-cols: 12 !default;\n$gutter-width: 1.5rem !default;\n$element-top-margin: $gutter-width/3 !default;\n$element-bottom-margin: ($gutter-width*2)/3 !default;\n\n\n// 13. Navigation Bar\n// ==========================================================================\n\n$navbar-height: 64px !default;\n$navbar-line-height: $navbar-height !default;\n$navbar-height-mobile: 56px !default;\n$navbar-line-height-mobile: $navbar-height-mobile !default;\n$navbar-font-size: 1rem !default;\n$navbar-font-color: #fff !default;\n$navbar-brand-font-size: 2.1rem !default;\n\n// 14. Side Navigation\n// ==========================================================================\n\n$sidenav-font-size: 14px !default;\n$sidenav-font-color: rgba(0,0,0,.87) !default;\n$sidenav-bg-color: #fff !default;\n$sidenav-padding: 16px !default;\n$sidenav-item-height: 48px !default;\n$sidenav-line-height: $sidenav-item-height !default;\n\n\n// 15. Photo Slider\n// ==========================================================================\n\n$slider-bg-color: color('grey', 'base') !default;\n$slider-bg-color-light: color('grey', 'lighten-2') !default;\n$slider-indicator-color: color('green', 'base') !default;\n\n\n// 16. Spinners | Loaders\n// ==========================================================================\n\n$spinner-default-color: $secondary-color !default;\n\n\n// 17. Tabs\n// ==========================================================================\n\n$tabs-underline-color: $primary-color-light !default;\n$tabs-text-color: $primary-color !default;\n$tabs-bg-color: #fff !default;\n\n\n// 18. Tables\n// ==========================================================================\n\n$table-border-color: #d0d0d0 !default;\n$table-striped-color: #f2f2f2 !default;\n\n\n// 19. Toasts\n// ==========================================================================\n\n$toast-height: 48px !default;\n$toast-color: #323232 !default;\n$toast-text-color: #fff !default;\n\n\n// 20. Typography\n// ==========================================================================\n\n$off-black: rgba(0, 0, 0, 0.87) !default;\n// Header Styles\n$h1-fontsize: 4.2rem !default;\n$h2-fontsize: 3.56rem !default;\n$h3-fontsize: 2.92rem !default;\n$h4-fontsize: 2.28rem !default;\n$h5-fontsize: 1.64rem !default;\n$h6-fontsize: 1rem !default;\n\n\n// 21. Footer\n// ==========================================================================\n\n$footer-bg-color: $primary-color !default;\n\n\n// 22. Flow Text\n// ==========================================================================\n\n$range : $large-screen - $small-screen !default;\n$intervals: 20 !default;\n$interval-size: $range / $intervals !default;\n\n\n// 23. Collections\n// ==========================================================================\n\n$collection-border-color: #e0e0e0 !default;\n$collection-bg-color: #fff !default;\n$collection-active-bg-color: $secondary-color !default;\n$collection-active-color: lighten($secondary-color, 55%) !default;\n$collection-hover-bg-color: #ddd !default;\n$collection-link-color: $secondary-color !default;\n$collection-line-height: 1.5rem !default;\n\n\n// 24. Progress Bar\n// ==========================================================================\n\n$progress-bar-color: $secondary-color !default;\n","// Badges\nspan.badge {\n  min-width: 3rem;\n  padding: 0 6px;\n  margin-left: 14px;\n  text-align: center;\n  font-size: 1rem;\n  line-height: $badge-height;\n  height: $badge-height;\n  color: color('grey', 'darken-1');\n  float: right;\n  box-sizing: border-box;\n\n  &.new {\n    font-weight: 300;\n    font-size: 0.8rem;\n    color: #fff;\n    background-color: $badge-bg-color;\n    border-radius: 2px;\n  }\n  &.new:after {\n    content: \" new\";\n  }\n\n  &[data-badge-caption]::after {\n    content: \" \" attr(data-badge-caption);\n  }\n}\nnav ul a span.badge {\n  display: inline-block;\n  float: none;\n  margin-left: 4px;\n  line-height: $badge-height;\n  height: $badge-height;\n}\n\n// Line height centering\n.collection-item span.badge {\n  margin-top: calc(#{$collection-line-height / 2} - #{$badge-height / 2});\n}\n.collapsible span.badge {\n  margin-top: calc(#{$collapsible-line-height / 2} - #{$badge-height / 2});\n}\n.side-nav span.badge {\n  margin-top: calc(#{$sidenav-line-height / 2} - #{$badge-height / 2});\n}\n","/* This is needed for some mobile phones to display the Google Icon font properly */\n.material-icons {\n  text-rendering: optimizeLegibility;\n  font-feature-settings: 'liga';\n}\n",".container {\n  margin: 0 auto;\n  max-width: 1280px;\n  width: 90%;\n}\n@media #{$medium-and-up} {\n  .container {\n    width: 85%;\n  }\n}\n@media #{$large-and-up} {\n  .container {\n    width: 70%;\n  }\n}\n.container .row {\n  margin-left: (-1 * $gutter-width / 2);\n  margin-right: (-1 * $gutter-width / 2);\n}\n\n.section {\n  padding-top: 1rem;\n  padding-bottom: 1rem;\n\n  &.no-pad {\n    padding: 0;\n  }\n  &.no-pad-bot {\n    padding-bottom: 0;\n  }\n  &.no-pad-top {\n    padding-top: 0;\n  }\n}\n\n\n// Mixins to eliminate code repitition\n@mixin reset-offset {\n  margin-left: auto;\n  left: auto;\n  right: auto;\n}\n@mixin grid-classes($size, $i, $perc) {\n  &.offset-#{$size}#{$i} {\n    margin-left: $perc;\n  }\n  &.pull-#{$size}#{$i} {\n    right: $perc;\n  }\n  &.push-#{$size}#{$i} {\n    left: $perc;\n  }\n}\n\n\n.row {\n  margin-left: auto;\n  margin-right: auto;\n  margin-bottom: 20px;\n\n  // Clear floating children\n  &:after {\n    content: \"\";\n    display: table;\n    clear: both;\n  }\n\n  .col {\n    float: left;\n    box-sizing: border-box;\n    padding: 0 $gutter-width / 2;\n    min-height: 1px;\n\n    &[class*=\"push-\"],\n    &[class*=\"pull-\"] {\n      position: relative;\n    }\n\n    $i: 1;\n    @while $i <= $num-cols {\n      $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n      &.s#{$i} {\n        width: $perc;\n        @include reset-offset;\n      }\n      $i: $i + 1;\n    }\n\n    $i: 1;\n    @while $i <= $num-cols {\n      $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n      @include grid-classes(\"s\", $i, $perc);\n      $i: $i + 1;\n    }\n\n    @media #{$medium-and-up} {\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        &.m#{$i} {\n          width: $perc;\n          @include reset-offset;\n        }\n        $i: $i + 1\n      }\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        @include grid-classes(\"m\", $i, $perc);\n        $i: $i + 1;\n      }\n    }\n\n    @media #{$large-and-up} {\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        &.l#{$i} {\n          width: $perc;\n          @include reset-offset;\n        }\n        $i: $i + 1;\n      }\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        @include grid-classes(\"l\", $i, $perc);\n        $i: $i + 1;\n      }\n    }\n\n    @media #{$extra-large-and-up} {\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        &.xl#{$i} {\n          width: $perc;\n          @include reset-offset;\n        }\n        $i: $i + 1;\n      }\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        @include grid-classes(\"xl\", $i, $perc);\n        $i: $i + 1;\n      }\n    }\n  }\n}\n","nav {\n  &.nav-extended {\n    height: auto;\n\n    .nav-wrapper {\n      min-height: $navbar-height-mobile;\n      height: auto;\n    }\n\n    .nav-content {\n      position: relative;\n      line-height: normal;\n    }\n  }\n\n  color: $navbar-font-color;\n  @extend .z-depth-1;\n  background-color: $primary-color;\n  width: 100%;\n  height: $navbar-height-mobile;\n  line-height: $navbar-line-height-mobile;\n\n  a { color: $navbar-font-color; }\n\n  i,\n  [class^=\"mdi-\"], [class*=\"mdi-\"],\n  i.material-icons {\n    display: block;\n    font-size: 24px;\n    height: $navbar-height-mobile;\n    line-height: $navbar-line-height-mobile;\n  }\n\n  .nav-wrapper {\n    position: relative;\n    height: 100%;\n  }\n\n  @media #{$large-and-up} {\n    a.button-collapse { display: none; }\n  }\n\n\n  // Collapse button\n  .button-collapse {\n    float: left;\n    position: relative;\n    z-index: 1;\n    height: $navbar-height-mobile;\n    margin: 0 18px;\n\n    i {\n      height: $navbar-height-mobile;\n      line-height: $navbar-line-height-mobile;\n    }\n  }\n\n\n  // Logo\n  .brand-logo {\n    position: absolute;\n    color: $navbar-font-color;\n    display: inline-block;\n    font-size: $navbar-brand-font-size;\n    padding: 0;\n    white-space: nowrap;\n\n    &.center {\n      left: 50%;\n      transform: translateX(-50%);\n    }\n\n    @media #{$medium-and-down} {\n      left: 50%;\n      transform: translateX(-50%);\n\n      &.left, &.right {\n        padding: 0;\n        transform: none;\n      }\n\n      &.left { left: 0.5rem; }\n      &.right {\n        right: 0.5rem;\n        left: auto;\n      }\n    }\n\n    &.right {\n      right: 0.5rem;\n      padding: 0;\n    }\n\n    i,\n    [class^=\"mdi-\"], [class*=\"mdi-\"],\n    i.material-icons {\n      float: left;\n      margin-right: 15px;\n    }\n  }\n\n\n  // Title\n  .nav-title {\n    display: inline-block;\n    font-size: 32px;\n    padding: 28px 0;\n  }\n\n\n  // Navbar Links\n  ul {\n    margin: 0;\n\n    li {\n      transition: background-color .3s;\n      float: left;\n      padding: 0;\n\n      &.active {\n        background-color: rgba(0,0,0,.1);\n      }\n    }\n    a {\n      transition: background-color .3s;\n      font-size: $navbar-font-size;\n      color: $navbar-font-color;\n      display: block;\n      padding: 0 15px;\n      cursor: pointer;\n\n      &.btn, &.btn-large, &.btn-flat, &.btn-floating {\n        margin-top: -2px;\n        margin-left: 15px;\n        margin-right: 15px;\n\n        & > .material-icons {\n          height: inherit;\n          line-height: inherit;\n        }\n      }\n\n      &:hover {\n        background-color: rgba(0,0,0,.1);\n      }\n    }\n\n    &.left {\n      float: left;\n    }\n  }\n\n  // Navbar Search Form\n  form {\n    height: 100%;\n  }\n\n  .input-field {\n    margin: 0;\n    height: 100%;\n\n    input {\n      height: 100%;\n      font-size: 1.2rem;\n      border: none;\n      padding-left: 2rem;\n\n      &:focus, &[type=text]:valid, &[type=password]:valid,\n      &[type=email]:valid, &[type=url]:valid, &[type=date]:valid {\n        border: none;\n        box-shadow: none;\n      }\n    }\n\n    label {\n      top: 0;\n      left: 0;\n\n      i {\n        color: rgba(255,255,255,.7);\n        transition: color .3s;\n      }\n      &.active i { color: $navbar-font-color; }\n    }\n  }\n}\n\n// Fixed Navbar\n.navbar-fixed {\n  position: relative;\n  height: $navbar-height-mobile;\n  z-index: 997;\n\n  nav {\n    position: fixed;\n  }\n}\n@media #{$medium-and-up} {\n  nav.nav-extended .nav-wrapper {\n    min-height: $navbar-height;\n  }\n  nav, nav .nav-wrapper i, nav a.button-collapse, nav a.button-collapse i {\n    height: $navbar-height;\n    line-height: $navbar-line-height;\n  }\n  .navbar-fixed {\n    height: $navbar-height;\n  }\n}\n","@font-face {\n    font-family: \"Roboto\";\n    src: local(Roboto Thin),\n        url(\"#{$roboto-font-path}Roboto-Thin.woff2\") format(\"woff2\"),\n        url(\"#{$roboto-font-path}Roboto-Thin.woff\") format(\"woff\");\n\n    font-weight: 100;\n}\n@font-face {\n    font-family: \"Roboto\";\n    src: local(Roboto Light),\n        url(\"#{$roboto-font-path}Roboto-Light.woff2\") format(\"woff2\"),\n        url(\"#{$roboto-font-path}Roboto-Light.woff\") format(\"woff\");\n    font-weight: 300;\n}\n\n@font-face {\n    font-family: \"Roboto\";\n    src: local(Roboto Regular), \n        url(\"#{$roboto-font-path}Roboto-Regular.woff2\") format(\"woff2\"),\n        url(\"#{$roboto-font-path}Roboto-Regular.woff\") format(\"woff\");\n    font-weight: 400;\n}\n\n@font-face {\n    font-family: \"Roboto\";\n    src: local(Roboto Medium),\n        url(\"#{$roboto-font-path}Roboto-Medium.woff2\") format(\"woff2\"),\n        url(\"#{$roboto-font-path}Roboto-Medium.woff\") format(\"woff\");\n    font-weight: 500;\n}\n\n@font-face {\n    font-family: \"Roboto\";\n    src: local(Roboto Bold),\n        url(\"#{$roboto-font-path}Roboto-Bold.woff2\") format(\"woff2\"),\n        url(\"#{$roboto-font-path}Roboto-Bold.woff\") format(\"woff\");\n    font-weight: 700;\n}\n","\na {\n  text-decoration: none;\n}\n\nhtml{\n  line-height: 1.5;\n\n  @media only screen and (min-width: 0) {\n    font-size: 14px;\n  }\n\n  @media only screen and (min-width: $medium-screen) {\n    font-size: 14.5px;\n  }\n\n  @media only screen and (min-width: $large-screen) {\n    font-size: 15px;\n  }\n\n  font-family: \"Roboto\", sans-serif;\n  font-weight: normal;\n  color: $off-black;\n}\nh1, h2, h3, h4, h5, h6 {\n\tfont-weight: 400;\n\tline-height: 1.1;\n}\n\n// Header Styles\nh1 a, h2 a, h3 a, h4 a, h5 a, h6 a { font-weight: inherit; }\nh1 { font-size: $h1-fontsize; line-height: 110%; margin: ($h1-fontsize / 2) 0 ($h1-fontsize / 2.5) 0;}\nh2 { font-size: $h2-fontsize; line-height: 110%; margin: ($h2-fontsize / 2) 0 ($h2-fontsize / 2.5) 0;}\nh3 { font-size: $h3-fontsize; line-height: 110%; margin: ($h3-fontsize / 2) 0 ($h3-fontsize / 2.5) 0;}\nh4 { font-size: $h4-fontsize; line-height: 110%; margin: ($h4-fontsize / 2) 0 ($h4-fontsize / 2.5) 0;}\nh5 { font-size: $h5-fontsize; line-height: 110%; margin: ($h5-fontsize / 2) 0 ($h5-fontsize / 2.5) 0;}\nh6 { font-size: $h6-fontsize; line-height: 110%; margin: ($h6-fontsize / 2) 0 ($h6-fontsize / 2.5) 0;}\n\n// Text Styles\nem { font-style: italic; }\nstrong { font-weight: 500; }\nsmall { font-size: 75%; }\n.light { font-weight: 300; }\n.thin { font-weight: 200; }\n\n\n.flow-text{\n  font-weight: 300;\n  $i: 0;\n  @while $i <= $intervals {\n    @media only screen and (min-width : 360 + ($i * $interval-size)) {\n      font-size: 1.2rem * (1 + (.02 * $i));\n    }\n    $i: $i + 1;\n  }\n\n  // Handle below 360px screen\n  @media only screen and (max-width: 360px) {\n    font-size: 1.2rem;\n  }\n}","// Scale transition\n.scale-transition {\n  &.scale-out {\n    transform: scale(0);\n    transition: transform .2s !important;\n  }\n\n  &.scale-in {\n    transform: scale(1);\n  }\n\n  transition: transform .3s cubic-bezier(0.53, 0.01, 0.36, 1.63) !important;\n}","\n\n.card-panel {\n  transition: box-shadow .25s;\n  padding: $card-padding;\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  border-radius: 2px;\n  @extend .z-depth-1;\n  background-color: $card-bg-color;\n}\n\n.card {\n  position: relative;\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  background-color: $card-bg-color;\n  transition: box-shadow .25s;\n  border-radius: 2px;\n  @extend .z-depth-1;\n\n\n  .card-title {\n    font-size: 24px;\n    font-weight: 300;\n    &.activator {\n      cursor: pointer;\n    }\n  }\n\n  // Card Sizes\n  &.small, &.medium, &.large {\n    position: relative;\n\n    .card-image {\n      max-height: 60%;\n      overflow: hidden;\n    }\n    .card-image + .card-content {\n      max-height: 40%;\n    }\n    .card-content {\n      max-height: 100%;\n      overflow: hidden;\n    }\n    .card-action {\n      position: absolute;\n      bottom: 0;\n      left: 0;\n      right: 0;\n    }\n  }\n\n  &.small {\n    height: 300px;\n  }\n\n  &.medium {\n    height: 400px;\n  }\n\n  &.large {\n    height: 500px;\n  }\n\n  // Horizontal Cards\n  &.horizontal {\n    &.small, &.medium, &.large {\n      .card-image {\n        height: 100%;\n        max-height: none;\n        overflow: visible;\n\n        img {\n          height: 100%;\n        }\n      }\n    }\n\n    display: flex;\n\n    .card-image {\n      max-width: 50%;\n      img {\n        border-radius: 2px 0 0 2px;\n        max-width: 100%;\n        width: auto;\n      }\n    }\n\n    .card-stacked {\n      display: flex;\n      flex-direction: column;\n      flex: 1;\n      position: relative;\n\n      .card-content {\n        flex-grow: 1;\n      }\n    }\n  }\n\n  // Sticky Action Section\n  &.sticky-action {\n    .card-action {\n      z-index: 2;\n    }\n\n    .card-reveal {\n      z-index: 1;\n      padding-bottom: 64px;\n    }\n  }\n\n\n\n\n  .card-image {\n    position: relative;\n\n    // Image background for content\n    img {\n      display: block;\n      border-radius: 2px 2px 0 0;\n      position: relative;\n      left: 0;\n      right: 0;\n      top: 0;\n      bottom: 0;\n      width: 100%;\n    }\n\n    .card-title {\n      color: $card-bg-color;\n      position: absolute;\n      bottom: 0;\n      left: 0;\n      max-width: 100%;\n      padding: $card-padding;\n    }\n  }\n\n  .card-content {\n    padding: $card-padding;\n    border-radius: 0 0 2px 2px;\n\n    p {\n      margin: 0;\n      color: inherit;\n    }\n    .card-title {\n      display: block;\n      line-height: 32px;\n      margin-bottom: 8px;\n\n      i {\n        line-height: 32px;\n      }\n    }\n  }\n\n  .card-action {\n    &:last-child {\n      border-radius: 0 0 2px 2px;\n    }\n    position: relative;\n    background-color: inherit;\n    border-top: 1px solid rgba(160,160,160,.2);\n    padding: 16px $card-padding;\n\n    a:not(.btn):not(.btn-large):not(.btn-floating) {\n      color: $card-link-color;\n      margin-right: $card-padding;\n      transition: color .3s ease;\n      text-transform: uppercase;\n\n      &:hover { color: $card-link-color-light; }\n    }\n  }\n\n  .card-reveal {\n    padding: $card-padding;\n    position: absolute;\n    background-color: $card-bg-color;\n    width: 100%;\n    overflow-y: auto;\n    left: 0;\n    top: 100%;\n    height: 100%;\n    z-index: 3;\n    display: none;\n\n    .card-title {\n      cursor: pointer;\n      display: block;\n    }\n  }\n}\n","#toast-container {\n  display:block;\n  position: fixed;\n  z-index: 10000;\n\n  @media #{$small-and-down} {\n    min-width: 100%;\n    bottom: 0%;\n  }\n  @media #{$medium-only} {\n    left: 5%;\n    bottom: 7%;\n    max-width: 90%;\n  }\n  @media #{$large-and-up} {\n    top: 10%;\n    right: 7%;\n    max-width: 86%;\n  }\n}\n\n.toast {\n  @extend .z-depth-1;\n  border-radius: 2px;\n  top: 35px;\n  width: auto;\n  clear: both;\n  margin-top: 10px;\n  position: relative;\n  max-width:100%;\n  height: auto;\n  min-height: $toast-height;\n  line-height: 1.5em;\n  word-break: break-all;\n  background-color: $toast-color;\n  padding: 10px 25px;\n  font-size: 1.1rem;\n  font-weight: 300;\n  color: $toast-text-color;\n\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n\n  .btn, .btn-flat {\n    margin: 0;\n    margin-left: 3rem;\n  }\n\n  &.rounded{\n    border-radius: 24px;\n  }\n\n  @media #{$small-and-down} {\n    width:100%;\n    border-radius: 0;\n  }\n  @media #{$medium-only} {\n    float: left;\n  }\n  @media #{$large-and-up} {\n    float: right;\n  }\n\n}\n",".tabs {\n  &.tabs-transparent {\n    background-color: transparent;\n\n    .tab a,\n    .tab.disabled a,\n    .tab.disabled a:hover {\n      color: rgba(255,255,255,0.7);\n    }\n\n    .tab a:hover,\n    .tab a.active {\n      color: #fff;\n    }\n\n    .indicator {\n      background-color: #fff;\n    }\n  }\n\n  &.tabs-fixed-width {\n    display: flex;\n\n    .tab {\n      -webkit-box-flex: 1;\n      -webkit-flex-grow: 1;\n          -ms-flex-positive: 1;\n              flex-grow: 1;\n    }\n  }\n\n  position: relative;\n  overflow-x: auto;\n  overflow-y: hidden;\n  height: 48px;\n  width: 100%;\n  background-color: $tabs-bg-color;\n  margin: 0 auto;\n  white-space: nowrap;\n\n  .tab {\n    display: inline-block;\n    text-align: center;\n    line-height: 48px;\n    height: 48px;\n    padding: 0;\n    margin: 0;\n    text-transform: uppercase;\n\n    a {\n      &:hover,\n      &.active {\n        background-color: transparent;\n        color: $tabs-text-color;\n      }\n\n      color: rgba($tabs-text-color, .7);\n      display: block;\n      width: 100%;\n      height: 100%;\n      padding: 0 24px;\n      font-size: 14px;\n      text-overflow: ellipsis;\n      overflow: hidden;\n      transition: color .28s ease;\n    }\n\n    &.disabled a,\n    &.disabled a:hover {\n      color: rgba($tabs-text-color, .7);\n      cursor: default;\n    }\n  }\n  .indicator {\n    position: absolute;\n    bottom: 0;\n    height: 2px;\n    background-color: $tabs-underline-color;\n    will-change: left, right;\n  }\n}\n\n// Fixed sideNav hide on smaller\n@media #{$medium-and-down} {\n  .tabs {\n    display: flex;\n\n    .tab {\n      -webkit-box-flex: 1;\n      -webkit-flex-grow: 1;\n      -ms-flex-positive: 1;\n      flex-grow: 1;\n\n      a {\n        padding: 0 12px;\n      }\n    }\n  }\n}\n",".material-tooltip {\n  padding: 10px 8px;\n  font-size: 1rem;\n  z-index: 2000;\n  background-color: transparent;\n  border-radius: 2px;\n  color: #fff;\n  min-height: 36px;\n  line-height: 120%;\n  opacity: 0;\n  position: absolute;\n  text-align: center;\n  max-width: calc(100% - 4px);\n  overflow: hidden;\n  left: 0;\n  top: 0;\n  pointer-events: none;\n  visibility: hidden;\n}\n\n.backdrop {\n  position: absolute;\n  opacity: 0;\n  height: 7px;\n  width: 14px;\n  border-radius: 0 0 50% 50%;\n  background-color: #323232;\n  z-index: -1;\n  transform-origin: 50% 0%;\n  visibility: hidden;\n}\n","// shared styles\n.btn,\n.btn-flat {\n  border: $button-border;\n  border-radius: $button-radius;\n  display: inline-block;\n  height: $button-height;\n  line-height: $button-height;\n  padding: $button-padding;\n  text-transform: uppercase;\n  vertical-align: middle;\n  // Gets rid of tap active state\n  -webkit-tap-highlight-color: transparent;\n}\n\n// Disabled shared style\n.btn.disabled,\n.btn-floating.disabled,\n.btn-large.disabled,\n.btn-flat.disabled,\n.btn:disabled,\n.btn-floating:disabled,\n.btn-large:disabled,\n.btn-flat:disabled,\n.btn[disabled],\n.btn-floating[disabled],\n.btn-large[disabled],\n.btn-flat[disabled] {\n  pointer-events: none;\n  background-color: $button-disabled-background !important;\n  box-shadow: none;\n  color: $button-disabled-color !important;\n  cursor: default;\n\n  &:hover {\n    background-color: $button-disabled-background !important;\n    color: $button-disabled-color !important;\n  }\n}\n\n// Shared icon styles\n.btn,\n.btn-floating,\n.btn-large,\n.btn-flat {\n  font-size: $button-font-size;\n  outline: 0;\n\n  i {\n    font-size: $button-icon-font-size;\n    line-height: inherit;\n  }\n}\n\n// Shared focus button style\n.btn,\n.btn-floating {\n  &:focus {\n    background-color: darken($button-raised-background, 10%);\n  }\n}\n\n// Raised Button\n.btn {\n  text-decoration: none;\n  color: $button-raised-color;\n  background-color: $button-raised-background;\n  text-align: center;\n  letter-spacing: .5px;\n  @extend .z-depth-1;\n  transition: .2s ease-out;\n  cursor: pointer;\n\n  &:hover {\n    background-color: $button-raised-background-hover;\n    @extend .z-depth-1-half;\n  }\n}\n\n// Floating button\n.btn-floating {\n  &:hover {\n    background-color: $button-floating-background-hover;\n    @extend .z-depth-1-half;\n  }\n\n  &:before {\n    border-radius: 0;\n  }\n\n  &.btn-large {\n    &.halfway-fab {\n      bottom: -$button-floating-large-size / 2;\n    }\n\n    width: $button-floating-large-size;\n    height: $button-floating-large-size;\n    i {\n      line-height: $button-floating-large-size;\n    }\n  }\n\n  &.halfway-fab {\n    &.left {\n      right: auto;\n      left: 24px;\n    }\n\n    position: absolute;\n    right: 24px;\n    bottom: -$button-floating-size / 2;\n  }\n\n  display: inline-block;\n  color: $button-floating-color;\n  position: relative;\n  overflow: hidden;\n  z-index: 1;\n  width: $button-floating-size;\n  height: $button-floating-size;\n  line-height: $button-floating-size;\n  padding: 0;\n  background-color: $button-floating-background;\n  border-radius: $button-floating-radius;\n  @extend .z-depth-1;\n  transition: .3s;\n  cursor: pointer;\n  vertical-align: middle;\n\n  i {\n    width: inherit;\n    display: inline-block;\n    text-align: center;\n    color: $button-floating-color;\n    font-size: $button-large-icon-font-size;\n    line-height: $button-floating-size;\n  }\n}\n\n// button fix\nbutton.btn-floating {\n  border: $button-border;\n}\n\n// Fixed Action Button\n.fixed-action-btn {\n  &.active {\n    ul {\n     visibility: visible;\n    }\n  }\n\n  &.horizontal {\n    padding: 0 0 0 15px;\n\n    ul {\n      text-align: right;\n      right: 64px;\n      top: 50%;\n      transform: translateY(-50%);\n      height: 100%;\n      left: auto;\n      width: 500px; /*width 100% only goes to width of button container */\n\n      li {\n        display: inline-block;\n        margin: 15px 15px 0 0;\n      }\n    }\n  }\n\n  &.toolbar {\n    &.active {\n      & > a i {\n        opacity: 0;\n      }\n    }\n\n    padding: 0;\n    height: $