xtarea\n   ========================================================================== */\n\n/* Style Placeholders */\n\n::-webkit-input-placeholder {\n  color: $placeholder-text-color;\n}\n\n:-moz-placeholder { /* Firefox 18- */\n  color: $placeholder-text-color;\n}\n\n::-moz-placeholder {  /* Firefox 19+ */\n  color: $placeholder-text-color;\n}\n\n:-ms-input-placeholder {\n  color: $placeholder-text-color;\n}\n\n/* Text inputs */\n\ninput:not([type]),\ninput[type=text],\ninput[type=password],\ninput[type=email],\ninput[type=url],\ninput[type=time],\ninput[type=date],\ninput[type=datetime],\ninput[type=datetime-local],\ninput[type=tel],\ninput[type=number],\ninput[type=search],\ntextarea.materialize-textarea {\n\n  // General Styles\n  background-color: transparent;\n  border: none;\n  border-bottom: $input-border;\n  border-radius: 0;\n  outline: none;\n  height: $input-height;\n  width: 100%;\n  font-size: $input-font-size;\n  margin: $input-margin;\n  padding: $input-padding;\n  box-shadow: none;\n  box-sizing: content-box;\n  transition: $input-transition;\n\n  // Disabled input style\n  &:disabled,\n  &[readonly=\"readonly\"] {\n    color: $input-disabled-color;\n    border-bottom: $input-disabled-border;\n  }\n\n  // Disabled label style\n  &:disabled+label,\n  &[readonly=\"readonly\"]+label {\n    color: $input-disabled-color;\n  }\n\n  // Focused input style\n  &:focus:not([readonly]) {\n    border-bottom: 1px solid $input-focus-color;\n    box-shadow: 0 1px 0 0 $input-focus-color;\n  }\n\n  // Focused label style\n  &:focus:not([readonly])+label {\n    color: $input-focus-color;\n  }\n\n  // Valid Input Style\n  &.valid,\n  &:focus.valid {\n    border-bottom: 1px solid $input-success-color;\n    box-shadow: 0 1px 0 0 $input-success-color;\n  }\n\n  // Custom Success Message\n  &.valid + label:after,\n  &:focus.valid + label:after {\n    content: attr(data-success);\n    color: $input-success-color;\n    opacity: 1;\n  }\n\n  // Invalid Input Style\n  &.invalid,\n  &:focus.invalid {\n    border-bottom: $input-invalid-border;\n    box-shadow: 0 1px 0 0 $input-error-color;\n  }\n\n  // Custom Error message\n  &.invalid + label:after,\n  &:focus.invalid + label:after {\n    content: attr(data-error);\n    color: $input-error-color;\n    opacity: 1;\n  }\n\n  // Full width label when using validate for error messages\n  &.validate + label {\n    width: 100%;\n    pointer-events: none;\n  }\n\n  // Form Message Shared Styles\n  & + label:after {\n    display: block;\n    content: \"\";\n    position: absolute;\n    top: 60px;\n    opacity: 0;\n    transition: .2s opacity ease-out, .2s color ease-out;\n  }\n}\n\n// Styling for input field wrapper\n.input-field {\n  // Inline styles\n  &.inline {\n    display: inline-block;\n    vertical-align: middle;\n    margin-left: 5px;\n\n    input,\n    .select-dropdown {\n      margin-bottom: 1rem;\n    }\n  }\n\n  // Gutter spacing\n  &.col {\n    label {\n      left: $gutter-width / 2;\n    }\n\n    .prefix ~ label,\n    .prefix ~ .validate ~ label {\n      width: calc(100% - 3rem - #{$gutter-width});\n    }\n  }\n\n  position: relative;\n  margin-top: 1rem;\n\n  label {\n    color: $input-border-color;\n    position: absolute;\n    top: 0.8rem;\n    left: 0;\n    font-size: 1rem;\n    cursor: text;\n    transition: .2s ease-out;\n    text-align: initial;\n\n    &:not(.label-icon).active {\n      font-size: $label-font-size;\n      transform: translateY(-140%);\n    }\n  }\n\n  // Prefix Icons\n  .prefix {\n    position: absolute;\n    width: $input-height;\n    font-size: 2rem;\n    transition: color .2s;\n\n    &.active { color: $input-focus-color; }\n  }\n\n  .prefix ~ input,\n  .prefix ~ textarea,\n  .prefix ~ label,\n  .prefix ~ .validate ~ label,\n  .prefix ~ .autocomplete-content {\n    margin-left: 3rem;\n    width: 92%;\n    width: calc(100% - 3rem);\n  }\n\n  .prefix ~ label { margin-left: 3rem; }\n\n  @media #{$medium-and-down} {\n    .prefix ~ input {\n      width: 86%;\n      width: calc(100% - 3rem);\n    }\n  }\n\n  @media #{$small-and-down} {\n    .prefix ~ input {\n      width: 80%;\n      width: calc(100% - 3rem);\n    }\n  }\n}\n\n\n/* Search Field */\n\n.input-field input[type=search] {\n  display: block;\n  line-height: inherit;\n  padding-left: 4rem;\n  width: calc(100% - 4rem);\n\n  &:focus {\n    background-color: $input-background;\n    border: 0;\n    box-shadow: none;\n    color: #444;\n\n    & + label i,\n    & ~ .mdi-navigation-close,\n    & ~ .material-icons {\n      color: #444;\n    }\n  }\n\n  & + label {\n    left: 1rem;\n  }\n\n  & ~ .mdi-navigation-close,\n  & ~ .material-icons {\n    position: absolute;\n    top: 0;\n    right: 1rem;\n    color: transparent;\n    cursor: pointer;\n    font-size: 2rem;\n    transition: .3s color;\n  }\n}\n\n\n/* Textarea */\n\n// Default textarea\ntextarea {\n  width: 100%;\n  height: $input-height;\n  background-color: transparent;\n\n  &.materialize-textarea {\n    overflow-y: hidden; /* prevents scroll bar flash */\n    padding: .8rem 0 1.6rem 0; /* prevents text jump on Enter keypress */\n    resize: none;\n    min-height: $input-height;\n  }\n}\n\n// For textarea autoresize\n.hiddendiv {\n  display: none;\n  white-space: pre-wrap;\n  word-wrap: break-word;\n  overflow-wrap: break-word; /* future version of deprecated 'word-wrap' */\n  padding-top: 1.2rem; /* prevents text jump on Enter keypress */\n\n  // Reduces repaints\n  position: absolute;\n  top: 0;\n}\n\n\n/* Autocomplete */\n.autocomplete-content {\n  margin-top: -1 * $input-margin-bottom;\n  display: block;\n  opacity: 1;\n  position: static;\n\n  li {\n    .highlight { color: #444; }\n\n    img {\n      height: $dropdown-item-height - 10;\n      width: $dropdown-item-height - 10;\n      margin: 5px 15px;\n    }\n  }\n}\n","/* Radio Buttons\n   ========================================================================== */\n\n// Remove default Radio Buttons\n[type=\"radio\"]:not(:checked),\n[type=\"radio\"]:checked {\n  position: absolute;\n  left: -9999px;\n  opacity: 0;\n}\n\n[type=\"radio\"]:not(:checked) + label,\n[type=\"radio\"]:checked + label {\n  position: relative;\n  padding-left: 35px;\n  cursor: pointer;\n  display: inline-block;\n  height: 25px;\n  line-height: 25px;\n  font-size: 1rem;\n  transition: .28s ease;\n\n  -khtml-user-select: none; /* webkit (konqueror) browsers */\n  user-select: none;\n}\n\n[type=\"radio\"] + label:before,\n[type=\"radio\"] + label:after {\n  content: '';\n  position: absolute;\n  left: 0;\n  top: 0;\n  margin: 4px;\n  width: 16px;\n  height: 16px;\n  z-index: 0;\n  transition: .28s ease;\n}\n\n/* Unchecked styles */\n[type=\"radio\"]:not(:checked) + label:before,\n[type=\"radio\"]:not(:checked) + label:after,\n[type=\"radio\"]:checked + label:before,\n[type=\"radio\"]:checked + label:after,\n[type=\"radio\"].with-gap:checked + label:before,\n[type=\"radio\"].with-gap:checked + label:after {\n  border-radius: 50%;\n}\n\n[type=\"radio\"]:not(:checked) + label:before,\n[type=\"radio\"]:not(:checked) + label:after {\n  border: 2px solid $radio-empty-color;\n}\n\n[type=\"radio\"]:not(:checked) + label:after {\n  transform: scale(0);\n}\n\n/* Checked styles */\n[type=\"radio\"]:checked + label:before {\n  border: 2px solid transparent;\n}\n\n[type=\"radio\"]:checked + label:after,\n[type=\"radio\"].with-gap:checked + label:before,\n[type=\"radio\"].with-gap:checked + label:after {\n  border: $radio-border;\n}\n\n[type=\"radio\"]:checked + label:after,\n[type=\"radio\"].with-gap:checked + label:after {\n  background-color: $radio-fill-color;\n}\n\n[type=\"radio\"]:checked + label:after {\n  transform: scale(1.02);\n}\n\n/* Radio With gap */\n[type=\"radio\"].with-gap:checked + label:after {\n  transform: scale(.5);\n}\n\n/* Focused styles */\n[type=\"radio\"].tabbed:focus + label:before {\n  box-shadow: 0 0 0 10px rgba(0,0,0,.1);\n}\n\n/* Disabled Radio With gap */\n[type=\"radio\"].with-gap:disabled:checked + label:before {\n  border: 2px solid $input-disabled-color;\n}\n\n[type=\"radio\"].with-gap:disabled:checked + label:after {\n  border: none;\n  background-color: $input-disabled-color;\n}\n\n/* Disabled style */\n[type=\"radio\"]:disabled:not(:checked) + label:before,\n[type=\"radio\"]:disabled:checked + label:before {\n  background-color: transparent;\n  border-color: $input-disabled-color;\n}\n\n[type=\"radio\"]:disabled + label {\n  color: $input-disabled-color;\n}\n\n[type=\"radio\"]:disabled:not(:checked) + label:before {\n  border-color: $input-disabled-color;\n}\n\n[type=\"radio\"]:disabled:checked + label:after {\n  background-color: $input-disabled-color;\n  border-color: $input-disabled-solid-color;\n}\n","/* Checkboxes\n   ========================================================================== */\n\n/* CUSTOM CSS CHECKBOXES */\nform p {\n  margin-bottom: 10px;\n  text-align: left;\n}\n\nform p:last-child {\n  margin-bottom: 0;\n}\n\n/* Remove default checkbox */\n[type=\"checkbox\"]:not(:checked),\n[type=\"checkbox\"]:checked {\n  position: absolute;\n  left: -9999px;\n  opacity: 0;\n}\n\n// Checkbox Styles\n[type=\"checkbox\"] {\n  // Text Label Style\n  + label {\n    position: relative;\n    padding-left: 35px;\n    cursor: pointer;\n    display: inline-block;\n    height: 25px;\n    line-height: 25px;\n    font-size: 1rem;\n\n    -webkit-user-select: none; /* webkit (safari, chrome) browsers */\n    -moz-user-select: none; /* mozilla browsers */\n    -khtml-user-select: none; /* webkit (konqueror) browsers */\n    -ms-user-select: none; /* IE10+ */\n  }\n\n  /* checkbox aspect */\n  + label:before,\n  &:not(.filled-in) + label:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 18px;\n    height: 18px;\n    z-index: 0;\n    border: 2px solid $radio-empty-color;\n    border-radius: 1px;\n    margin-top: 2px;\n    transition: .2s;\n  }\n\n  &:not(.filled-in) + label:after {\n    border: 0;\n    transform: scale(0);\n  }\n\n  &:not(:checked):disabled + label:before {\n    border: none;\n    background-color: $input-disabled-color;\n  }\n\n  // Focused styles\n  &.tabbed:focus + label:after {\n    transform: scale(1);\n    border: 0;\n    border-radius: 50%;\n    box-shadow: 0 0 0 10px rgba(0,0,0,.1);\n    background-color: rgba(0,0,0,.1);\n  }\n}\n\n[type=\"checkbox\"]:checked {\n  + label:before {\n    top: -4px;\n    left: -5px;\n    width: 12px;\n    height: 22px;\n    border-top: 2px solid transparent;\n    border-left: 2px solid transparent;\n    border-right: $radio-border;\n    border-bottom: $radio-border;\n    transform: rotate(40deg);\n    backface-visibility: hidden;\n    transform-origin: 100% 100%;\n  }\n\n  &:disabled + label:before {\n    border-right: 2px solid $input-disabled-color;\n    border-bottom: 2px solid $input-disabled-color;\n  }\n}\n\n/* Indeterminate checkbox */\n[type=\"checkbox\"]:indeterminate {\n  +label:before {\n    top: -11px;\n    left: -12px;\n    width: 10px;\n    height: 22px;\n    border-top: none;\n    border-left: none;\n    border-right: $radio-border;\n    border-bottom: none;\n    transform: rotate(90deg);\n    backface-visibility: hidden;\n    transform-origin: 100% 100%;\n  }\n\n  // Disabled indeterminate\n  &:disabled + label:before {\n    border-right: 2px solid $input-disabled-color;\n    background-color: transparent;\n  }\n}\n\n// Filled in Style\n[type=\"checkbox\"].filled-in {\n  // General\n  + label:after {\n    border-radius: 2px;\n  }\n\n  + label:before,\n  + label:after {\n    content: '';\n    left: 0;\n    position: absolute;\n    /* .1s delay is for check animation */\n    transition: border .25s, background-color .25s, width .20s .1s, height .20s .1s, top .20s .1s, left .20s .1s;\n    z-index: 1;\n  }\n\n  // Unchecked style\n  &:not(:checked) + label:before {\n    width: 0;\n    height: 0;\n    border: 3px solid transparent;\n    left: 6px;\n    top: 10px;\n\n    -webkit-transform: rotateZ(37deg);\n    transform: rotateZ(37deg);\n    -webkit-transform-origin: 20% 40%;\n    transform-origin: 100% 100%;\n  }\n\n  &:not(:checked) + label:after {\n    height: 20px;\n    width: 20px;\n    background-color: transparent;\n    border: 2px solid $radio-empty-color;\n    top: 0px;\n    z-index: 0;\n  }\n\n  // Checked style\n  &:checked {\n    + label:before {\n      top: 0;\n      left: 1px;\n      width: 8px;\n      height: 13px;\n      border-top: 2px solid transparent;\n      border-left: 2px solid transparent;\n      border-right: 2px solid $input-background;\n      border-bottom: 2px solid $input-background;\n      -webkit-transform: rotateZ(37deg);\n      transform: rotateZ(37deg);\n\n      -webkit-transform-origin: 100% 100%;\n      transform-origin: 100% 100%;\n    }\n\n    + label:after {\n      top: 0;\n      width: 20px;\n      height: 20px;\n      border: 2px solid $secondary-color;\n      background-color: $secondary-color;\n      z-index: 0;\n    }\n  }\n\n  // Focused styles\n  &.tabbed:focus + label:after {\n    border-radius: 2px;\n    border-color: $radio-empty-color;\n    background-color: rgba(0,0,0,.1);\n  }\n\n  &.tabbed:checked:focus + label:after {\n    border-radius: 2px;\n    background-color: $secondary-color;\n    border-color: $secondary-color;\n  }\n\n  // Disabled style\n  &:disabled:not(:checked) + label:before {\n    background-color: transparent;\n    border: 2px solid transparent;\n  }\n\n  &:disabled:not(:checked) + label:after {\n    border-color: transparent;\n    background-color: $input-disabled-solid-color;\n  }\n\n  &:disabled:checked + label:before {\n    background-color: transparent;\n  }\n\n  &:disabled:checked + label:after {\n    background-color: $input-disabled-solid-color;\n    border-color: $input-disabled-solid-color;\n  }\n}\n","/* Switch\n   ========================================================================== */\n\n.switch,\n.switch * {\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -khtml-user-select: none;\n  -ms-user-select: none;\n}\n\n.switch label {\n  cursor: pointer;\n}\n\n.switch label input[type=checkbox] {\n  opacity: 0;\n  width: 0;\n  height: 0;\n\n  &:checked + .lever {\n    background-color: $switch-checked-lever-bg;\n\n    &:after {\n      background-color: $switch-bg-color;\n      left: 24px;\n    }\n  }\n}\n\n.switch label .lever {\n  content: \"\";\n  display: inline-block;\n  position: relative;\n  width: 40px;\n  height: 15px;\n  background-color: $switch-unchecked-lever-bg;\n  border-radius: $switch-radius;\n  margin-right: 10px;\n  transition: background 0.3s ease;\n  vertical-align: middle;\n  margin: 0 16px;\n\n  &:after {\n    content: \"\";\n    position: absolute;\n    display: inline-block;\n    width: 21px;\n    height: 21px;\n    background-color: $switch-unchecked-bg;\n    border-radius: 21px;\n    box-shadow: 0 1px 3px 1px rgba(0,0,0,.4);\n    left: -5px;\n    top: -3px;\n    transition: left 0.3s ease, background .3s ease, box-shadow 0.1s ease;\n  }\n}\n\n// Switch active style\ninput[type=checkbox]:checked:not(:disabled) ~ .lever:active::after,\ninput[type=checkbox]:checked:not(:disabled).tabbed:focus ~ .lever::after {\n  box-shadow: 0 1px 3px 1px rgba(0,0,0,.4), 0 0 0 15px transparentize($switch-bg-color, .9);\n}\n\ninput[type=checkbox]:not(:disabled) ~ .lever:active:after,\ninput[type=checkbox]:not(:disabled).tabbed:focus ~ .lever::after {\n  box-shadow: 0 1px 3px 1px rgba(0,0,0,.4), 0 0 0 15px rgba(0, 0, 0, .08);\n}\n\n// Disabled Styles\n.switch input[type=checkbox][disabled] + .lever {\n  cursor: default;\n}\n\n.switch label input[type=checkbox][disabled] + .lever:after,\n.switch label input[type=checkbox][disabled]:checked + .lever:after {\n  background-color: $input-disabled-solid-color;\n}\n","/* Select Field\n   ========================================================================== */\n\nselect { display: none; }\nselect.browser-default { display: block; }\n\nselect {\n  background-color: $select-background;\n  width: 100%;\n  padding: $select-padding;\n  border: $select-border;\n  border-radius: $select-radius;\n  height: $input-height;\n}\n\n.select-label {\n  position: absolute;\n}\n\n.select-wrapper {\n  position: relative;\n\n  input.select-dropdown {\n    position: relative;\n    cursor: pointer;\n    background-color: transparent;\n    border: none;\n    border-bottom: $input-border;\n    outline: none;\n    height: $input-height;\n    line-height: $input-height;\n    width: 100%;\n    font-size: $input-font-size;\n    margin: $input-margin;\n    padding: 0;\n    display: block;\n  }\n\n  span.caret {\n    color: initial;\n    position: absolute;\n    right: 0;\n    top: 0;\n    bottom: 0;\n    height: 10px;\n    margin: auto 0;\n    font-size: 10px;\n    line-height: 10px;\n\n    &.disabled {\n      color: $input-disabled-color;\n    }\n  }\n\n  & + label {\n    position: absolute;\n    top: -14px;\n    font-size: $label-font-size;\n  }\n}\n\n// Disabled styles\nselect:disabled {\n  color: rgba(0,0,0,.3);\n}\n\n.select-wrapper input.select-dropdown:disabled {\n  color: rgba(0,0,0,.3);\n  cursor: default;\n  -webkit-user-select: none; /* webkit (safari, chrome) browsers */\n  -moz-user-select: none; /* mozilla browsers */\n  -ms-user-select: none; /* IE10+ */\n  border-bottom: 1px solid rgba(0,0,0,.3);\n}\n\n.select-wrapper i {\n  color: $select-disabled-color;\n}\n\n.select-dropdown li.disabled,\n.select-dropdown li.disabled > span,\n.select-dropdown li.optgroup {\n  color: $select-disabled-color;\n  background-color: transparent;\n}\n\n// Prefix Icons\n.prefix ~ .select-wrapper {\n  margin-left: 3rem;\n  width: 92%;\n  width: calc(100% - 3rem);\n}\n\n.prefix ~ label { margin-left: 3rem; }\n\n// Icons\n.select-dropdown li {\n  img {\n    height: $dropdown-item-height - 10;\n    width: $dropdown-item-height - 10;\n    margin: 5px 15px;\n    float: right;\n  }\n}\n\n// Optgroup styles\n.select-dropdown li.optgroup {\n  border-top: 1px solid $dropdown-hover-bg-color;\n\n  &.selected > span {\n    color: rgba(0, 0, 0, .7);\n  }\n\n  & > span {\n    color: rgba(0, 0, 0, .4);\n  }\n\n  & ~ li.optgroup-option {\n    padding-left: 1rem;\n  }\n}\n","/* File Input\n   ========================================================================== */\n\n.file-field {\n  position: relative;\n\n  .file-path-wrapper {\n    overflow: hidden;\n    padding-left: 10px;\n  }\n\n  input.file-path { width: 100%; }\n\n  .btn {\n    float: left;\n    height: $input-height;\n    line-height: $input-height;\n  }\n\n  span {\n    cursor: pointer;\n  }\n\n  input[type=file] {\n    position: absolute;\n    top: 0;\n    right: 0;\n    left: 0;\n    bottom: 0;\n    width: 100%;\n    margin: 0;\n    padding: 0;\n    font-size: 20px;\n    cursor: pointer;\n    opacity: 0;\n    filter: alpha(opacity=0);\n  }\n}\n","/* Range\n   ========================================================================== */\n\n.range-field {\n  position: relative;\n}\n\ninput[type=range],\ninput[type=range] + .thumb {\n  @extend .no-select;\n  cursor: pointer;\n}\n\ninput[type=range] {\n  position: relative;\n  background-color: transparent;\n  border: none;\n  outline: none;\n  width: 100%;\n  margin: 15px 0;\n  padding: 0;\n\n  &:focus {\n    outline: none;\n  }\n}\n\ninput[type=range] + .thumb {\n  position: absolute;\n  top: 10px;\n  left: 0;\n  border: none;\n  height: 0;\n  width: 0;\n  border-radius: 50%;\n  background-color: $radio-fill-color;\n  margin-left: 7px;\n\n  transform-origin: 50% 50%;\n  transform: rotate(-45deg);\n\n  .value {\n    display: block;\n    width: 30px;\n    text-align: center;\n    color: $radio-fill-color;\n    font-size: 0;\n    transform: rotate(45deg);\n  }\n\n  &.active {\n    border-radius: 50% 50% 50% 0;\n\n    .value {\n      color: $input-background;\n      margin-left: -1px;\n      margin-top: 8px;\n      font-size: 10px;\n    }\n  }\n}\n\n// WebKit\ninput[type=range] {\n  -webkit-appearance: none;\n}\n\ninput[type=range]::-webkit-slider-runnable-track {\n  height: $track-height;\n  background: #c2c0c2;\n  border: none;\n}\n\ninput[type=range]::-webkit-slider-thumb {\n  -webkit-appearance: none;\n  border: none;\n  height: $range-height;\n  width: $range-width;\n  border-radius: 50%;\n  background-color: $radio-fill-color;\n  transform-origin: 50% 50%;\n  margin: -5px 0 0 0;\n  transition: .3s;\n}\n\ninput[type=range]:focus::-webkit-slider-runnable-track {\n  background: #ccc;\n}\n\n// FireFox\ninput[type=range] {\n  /* fix for FF unable to apply focus style bug  */\n  border: 1px solid white;\n\n  /*required for proper track sizing in FF*/\n}\n\ninput[type=range]::-moz-range-track {\n  height: $track-height;\n  background: #ddd;\n  border: none;\n}\n\ninput[type=range]::-moz-range-thumb {\n  border: none;\n  height: $range-height;\n  width: $range-width;\n  border-radius: 50%;\n  background: $radio-fill-color;\n  margin-top: -5px;\n}\n\n// hide the outline behind the border\ninput[type=range]:-moz-focusring {\n  outline: 1px solid #fff;\n  outline-offset: -1px;\n}\n\ninput[type=range]:focus::-moz-range-track {\n  background: #ccc;\n}\n\n// IE 10+\ninput[type=range]::-ms-track {\n  height: $track-height;\n\n  // remove bg colour from the track, we'll use ms-fill-lower and ms-fill-upper instead\n  background: transparent;\n\n  // leave room for the larger thumb to overflow with a transparent border */\n  border-color: transparent;\n  border-width: 6px 0;\n\n  /*remove default tick marks*/\n  color: transparent;\n}\n\ninput[type=range]::-ms-fill-lower {\n  background: #777;\n}\n\ninput[type=range]::-ms-fill-upper {\n  background: #ddd;\n}\n\ninput[type=range]::-ms-thumb {\n  border: none;\n  height: $range-height;\n  width: $range-width;\n  border-radius: 50%;\n  background: $radio-fill-color;\n}\n\ninput[type=range]:focus::-ms-fill-lower {\n  background: #888;\n}\n\ninput[type=range]:focus::-ms-fill-upper {\n  background: #ccc;\n}\n","/***************\n    Nav List\n***************/\n.table-of-contents {\n  &.fixed {\n      position: fixed;\n    }\n\n  li {\n    padding: 2px 0;\n  }\n  a {\n    display: inline-block;\n    font-weight: 300;\n    color: #757575;\n    padding-left: 20px;\n    height: 1.5rem;\n    line-height: 1.5rem;\n    letter-spacing: .4;\n    display: inline-block;\n\n    &:hover {\n      color: lighten(#757575, 20%);\n      padding-left: 19px;\n      border-left: 1px solid $primary-color;\n    }\n    &.active {\n      font-weight: 500;\n      padding-left: 18px;\n      border-left: 2px solid $primary-color;\n    }\n  }\n}\n",".side-nav {\n  position: fixed;\n  width: 300px;\n  left: 0;\n  top: 0;\n  margin: 0;\n  transform: translateX(-100%);\n  height: 100%;\n  height: calc(100% + 60px);\n  height: -moz-calc(100%); //Temporary Firefox Fix\n  padding-bottom: 60px;\n  background-color: $sidenav-bg-color;\n  z-index: 999;\n  overflow-y: auto;\n  will-change: transform;\n  backface-visibility: hidden;\n  transform: translateX(-105%);\n\n  @extend .z-depth-1;\n\n  // Right Align\n  &.right-aligned {\n    right: 0;\n    transform: translateX(105%);\n    left: auto;\n    transform: translateX(100%);\n  }\n\n  .collapsible {\n    margin: 0;\n  }\n\n\n  li {\n    float: none;\n    line-height: $sidenav-line-height;\n\n    &.active { background-color: rgba(0,0,0,.05); }\n  }\n\n  li > a {\n    color: $sidenav-font-color;\n    display: block;\n    font-size: $sidenav-font-size;\n    font-weight: 500;\n    height: $sidenav-item-height;\n    line-height: $sidenav-line-height;\n    padding: 0 ($sidenav-padding * 2);\n\n    &:hover { background-color: rgba(0,0,0,.05);}\n\n    &.btn, &.btn-large, &.btn-flat, &.btn-floating {\n      margin: 10px 15px;\n    }\n\n    &.btn,\n    &.btn-large,\n    &.btn-floating { color: $button-raised-color; }\n    &.btn-flat { color: $button-flat-color; }\n\n    &.btn:hover,\n    &.btn-large:hover { background-color: lighten($button-raised-background, 5%); }\n    &.btn-floating:hover { background-color: $button-raised-background; }\n\n    & > i,\n    & > [class