n {\n  from { -webkit-transform: rotate(-130deg); }\n  50% { -webkit-transform: rotate(5deg); }\n  to { -webkit-transform: rotate(-130deg); }\n}\n\n@keyframes right-spin {\n  from { transform: rotate(-130deg); }\n  50% { transform: rotate(5deg); }\n  to { transform: rotate(-130deg); }\n}\n\n#spinnerContainer.cooldown {\n  /* duration: SHRINK_TIME */\n  -webkit-animation: container-rotate 1568ms linear infinite, fade-out 400ms cubic-bezier(0.4, 0.0, 0.2, 1);\n  animation: container-rotate 1568ms linear infinite, fade-out 400ms cubic-bezier(0.4, 0.0, 0.2, 1);\n}\n\n@-webkit-keyframes fade-out {\n  from { opacity: 1; }\n  to { opacity: 0; }\n}\n\n@keyframes fade-out {\n  from { opacity: 1; }\n  to { opacity: 0; }\n}\n",".slider {\n  position: relative;\n  height: 400px;\n  width: 100%;\n\n  // Fullscreen slider\n  &.fullscreen {\n    height: 100%;\n    width: 100%;\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n\n    ul.slides {\n      height: 100%;\n    }\n\n    ul.indicators {\n      z-index: 2;\n      bottom: 30px;\n    }\n  }\n\n  .slides {\n    background-color: $slider-bg-color;\n    margin: 0;\n    height: 400px;\n\n    li {\n      opacity: 0;\n      position: absolute;\n      top: 0;\n      left: 0;\n      z-index: 1;\n      width: 100%;\n      height: inherit;\n      overflow: hidden;\n\n      img {\n        height: 100%;\n        width: 100%;\n        background-size: cover;\n        background-position: center;\n      }\n\n      .caption {\n        color: #fff;\n        position: absolute;\n        top: 15%;\n        left: 15%;\n        width: 70%;\n        opacity: 0;\n\n        p { color: $slider-bg-color-light; }\n      }\n\n      &.active {\n        z-index: 2;\n      }\n    }\n  }\n\n\n  .indicators {\n    position: absolute;\n    text-align: center;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    margin: 0;\n\n    .indicator-item {\n      display: inline-block;\n      position: relative;\n      cursor: pointer;\n 